export default {
  /*
    这个mixins相当于把当前的loginConfirm插入导入使用的组件内部，所以访问loginConfirm方法时，需要在组件中使用this.loginConfirm()
    */
  methods: {
    loginConfirm () {
      if (!this.$store.state.user.userInfo.token) { // 未登录，弹窗提示
        console.log('未登录')
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能继续操作哦',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // on confirm
            this.$router.replace(`/login?returnPath=${this.$route.fullPath}`)
          })
          .catch(() => {
            // on cancel
          })
        return true
      }
      return false
    }
  }
}
